export const loadBookmarked = (collection) => async (dispatch) => {
  dispatch({
    type: "GET_BOOKMARKED",
    payload: {
      data: collection,
    },
  });
};

export const initialLoadBookmarked = () => async (dispatch) => {
  try {
    const collection =
      (await JSON.parse(localStorage.getItem("collection"))) || [];
    dispatch({
      type: "GET_BOOKMARKED",
      payload: {
        data: collection,
      },
    });
  } catch (err) {
    console.log(err);
  }
};
