import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import GameDetail from "../components/GameDetail";
//Components
import Game from "../components/Game";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { loadGames } from "../actions/gamesAction";
import { initialLoadBookmarked } from "../actions/bookmarkedAction";
//Styling and Animation
import styled from "styled-components";
import { motion } from "framer-motion";
import { fadeIn } from "../animations";
//Images
import plus from "../img/plus.png";
import minus from "../img/minus.png";

const Home = () => {
  //Get The Current Location
  const location = useLocation();
  const pathId = location.pathname.split("/")[2];
  //Fetch games
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(loadGames());
  }, [dispatch]);
  useEffect(() => {
    dispatch(initialLoadBookmarked());
  }, [dispatch]);
  //Get That Data Back
  const { popular, newGames, upcoming, searched } = useSelector(
    (state) => state.games
  );
  const { collection } = useSelector((state) => state.bookmarked);
  //Style If Only 1 Saved Game
  const style = collection.length === 1 ? { maxWidth: "600px" } : {};
  //Toggle On/Off Saved Games
  const [toggle, setToggle] = useState(false);
  return (
    <GameList variants={fadeIn} initial="hidden" animate="show">
      {pathId && <GameDetail />}
      <h2>
        Saved Games ({collection.length})
        <span onClick={() => setToggle(!toggle)}>
          <ToggleImage
            src={toggle && collection.length ? minus : plus}
            alt="show/hide saved games"
          />
        </span>
      </h2>
      {collection.length && toggle ? (
        <div>
          <Games style={style}>
            {collection.map((game) => (
              <Game
                name={game.name}
                released={game.released}
                id={game.id}
                image={game.background_image}
                key={"saved " + game.id}
              />
            ))}
          </Games>
        </div>
      ) : (
        ""
      )}
      {searched.length ? (
        <div className="searched">
          <h2>Searched Games</h2>
          <Games>
            {searched.map((game) => (
              <Game
                name={game.name}
                released={game.released}
                id={game.id}
                image={game.background_image}
                key={game.id}
              />
            ))}
          </Games>
        </div>
      ) : (
        ""
      )}
      <h2>Upcoming Games</h2>
      <Games>
        {upcoming.map((game) => (
          <Game
            name={game.name}
            released={game.released}
            id={game.id}
            image={game.background_image}
            key={game.id}
          />
        ))}
      </Games>
      <h2>Popular Games</h2>
      <Games>
        {popular.map((game) => (
          <Game
            name={game.name}
            released={game.released}
            id={game.id}
            image={game.background_image}
            key={game.id}
          />
        ))}
      </Games>
      <h2>New Games</h2>
      <Games>
        {newGames.map((game) => (
          <Game
            name={game.name}
            released={game.released}
            id={game.id}
            image={game.background_image}
            key={game.id}
          />
        ))}
      </Games>
    </GameList>
  );
};

const GameList = styled(motion.div)`
  padding: 0rem 5rem;
  h2 {
    padding: 5rem 0rem;
  }

  @media (max-width: 440px) {
    padding: 0rem 2rem;
  }
`;

const Games = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  grid-column-gap: 3rem;
  grid-row-gap: 5rem;

  @media (max-width: 1220px) {
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  }

  @media (max-width: 1020px) {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
`;

const ToggleImage = styled.img`
  display: inline;
  margin-left: 1rem;
  cursor: pointer;
`;

export default Home;
