import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { loadBookmarked } from "../actions/bookmarkedAction";
//Styling and Animation
import styled from "styled-components";
import { motion } from "framer-motion";
import { smallImage } from "../util";
//Images
import playstation from "../img/playstation.svg";
import steam from "../img/steam.svg";
import xbox from "../img/xbox.svg";
import nintendo from "../img/nintendo.svg";
import apple from "../img/apple.svg";
import gamepad from "../img/gamepad.svg";
import starEmpty from "../img/star-empty.png";
import starFull from "../img/star-full.png";

const GameDetail = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const [isSaved, setIsSaved] = useState(false);
  //Exit Detail Logic
  const exitDetailHandlerOnClick = (e) => {
    const element = e.target;
    if (element.classList.contains("shadow")) {
      exitDetail();
    }
  };
  const exitDetailHandlerOnKey = (e) => {
    if (e.keyCode === 27) {
      window.removeEventListener("keydown", exitDetailHandlerOnKey);
      exitDetail();
    }
  };
  //Exit Detail
  const exitDetail = () => {
    document.body.style.overflow = "auto";
    history.push("/");
  };
  //Get Stars
  const getStars = () => {
    const stars = [];
    const rating = Math.floor(game.rating);
    for (let i = 1; i <= 5; i++) {
      if (i <= rating) {
        stars.push(<img src={starFull} alt="star" key={i} />);
      } else {
        stars.push(<img src={starEmpty} alt="star" key={i} />);
      }
    }
    return stars;
  };
  //Get Platform Images
  const getPlatform = (platform) => {
    switch (platform) {
      case "PlayStation 4":
        return playstation;
      case "PlayStation 5":
        return playstation;
      case "Xbox One":
        return xbox;
      case "Xbox Series S/X":
        return xbox;
      case "PC":
        return steam;
      case "Nintendo Switch":
        return nintendo;
      case "iOS":
        return apple;
      default:
        return gamepad;
    }
  };
  //Data
  const { screen, game, isLoading } = useSelector((state) => state.detail);
  const { collection } = useSelector((state) => state.bookmarked);
  //Save To Local Storage
  const saveToCollection = () => {
    try {
      let collection = JSON.parse(localStorage.getItem("collection")) || [];
      if (collection.find((savedGame) => savedGame.id === game.id)) {
        return;
      }
      collection.push(game);
      const serializedState = JSON.stringify(collection);
      localStorage.setItem("collection", serializedState);
      dispatch(loadBookmarked(collection));
      exitDetail();
    } catch (err) {
      console.log(err);
    }
  };
  //Remove From Local Storage
  const removeFromCollection = () => {
    try {
      let collection = JSON.parse(localStorage.getItem("collection")) || [];
      if (collection.find((savedGame) => savedGame.id === game.id)) {
        let index = collection.findIndex(
          (collectionGame) => collectionGame.id === game.id
        );
        collection.splice(index, 1);
        const serializedState = JSON.stringify(collection);
        localStorage.setItem("collection", serializedState);
        dispatch(loadBookmarked(collection));
        exitDetail();
      }
    } catch (err) {
      console.log(err);
    }
  };
  //Check If Game Is Saved
  useEffect(() => {
    if (collection.find((savedGame) => savedGame.id === game.id)) {
      setIsSaved(true);
    } else {
      setIsSaved(false);
    }
  }, [collection, game.id]);
  // Listen For Key Down
  window.addEventListener("keydown", exitDetailHandlerOnKey);
  return (
    <>
      {!isLoading && (
        <CardShadow className="shadow" onClick={exitDetailHandlerOnClick}>
          <Detail>
            <Stats>
              <div className="rating">
                <motion.h3>{game.name}</motion.h3>
                <p>Rating: {game.rating}</p>
                {getStars()}
              </div>
              <Info>
                <h3>Platforms</h3>
                <Platforms>
                  {game.platforms.map((data) => (
                    <div className="platform-details" key={data.platform.id}>
                      <img
                        src={getPlatform(data.platform.name)}
                        alt={data.platform.name}
                      />
                      <p>{data.platform.name}</p>
                    </div>
                  ))}
                </Platforms>
              </Info>
            </Stats>
            <Save>
              <button
                onClick={isSaved ? removeFromCollection : saveToCollection}
              >
                {isSaved ? "Remove from Collection" : "Save to Collection"}
              </button>
            </Save>
            <Media>
              {game.background_image ? (
                <motion.img
                  src={smallImage(game.background_image, 1280)}
                  alt={game.name}
                />
              ) : null}
            </Media>
            <Description>
              <p>{game.description_raw}</p>
            </Description>
            <div className="gallery">
              {screen.results.map((screen) => (
                <img
                  src={smallImage(screen.image, 1280)}
                  key={screen.id}
                  alt="screenshot"
                />
              ))}
            </div>
          </Detail>
        </CardShadow>
      )}
    </>
  );
};

const CardShadow = styled(motion.div)`
  width: 100%;
  min-height: 100vh;
  overflow-y: scroll;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;

  &::-webkit-scrollbar {
    width: 0.5rem;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #ff7676;
  }
  &::-webkit-scrollbar-track {
    background-color: #fff;
  }
`;

const Detail = styled(motion.div)`
  width: 80%;
  border-radius: 1rem;
  padding: 2rem 5rem;
  background-color: #fff;
  position: absolute;
  left: 10%;
  color: #000;
  z-index: 10;

  img {
    width: 100%;
  }

  @media (max-width: 800px) {
    width: 90%;
    left: 5%;
  }

  @media (max-width: 600px) {
    padding: 1rem 2rem;
  }
`;

const Stats = styled(motion.div)`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  img {
    width: 2rem;
    height: 2rem;
    display: inline;
  }

  @media (max-width: 700px) {
    flex-direction: column;
  }
`;

const Info = styled(motion.div)`
  text-align: center;
  min-width: 20vw;

  @media (max-width: 700px) {
    text-align: left;
  }
`;

const Platforms = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(70px, 1fr));
  grid-row-gap: 10px;
  justify-content: space-evenly;
  .platform-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 2rem;

    p {
      font-size: 0.8rem;
      color: rgba(105, 105, 105, 0.8);
    }
  }

  @media (max-width: 700px) {
    display: flex;
    flex-direction: column;
    .platform-details {
      flex-direction: row;
      margin-left: 0;

      p {
        margin-left: 1rem;
      }
    }
  }
`;

const Save = styled(motion.div)`
  margin-top: 1rem;

  button {
    background-color: transparent;
    outline: none;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid #333;
    cursor: pointer;

    :hover {
      color: rgba(105, 105, 105, 0.8);
      border-bottom: 1px solid rgba(105, 105, 105, 0.8);
    }
  }
`;

const Media = styled(motion.div)`
  margin-top: 5rem;
`;

const Description = styled(motion.div)`
  margin: 5rem 0rem;
`;

export default GameDetail;
